/* HomeStyles.css */

.card {
    border: none;
    border-radius: 12px;
    box-shadow: 0px 8px 16px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease;
    overflow: hidden;
}

.card:hover {
    transform: translateY(-5px);
    /* Elevates card slightly on hover */
}

.card-body {
    padding: 16px;
    background-color: #fff;
}

.button-group {
    display: flex;
    gap: 10px;
    /* Added gap between buttons */
    margin-top: 10px;
}

.more-details-btn {
    background-color: #3e4095;
    /* Deep luxurious color for primary action */
    color: white;
}

.add-cart-btn {
    background-color: #d4d4d4;
    /* Softer contrast for secondary action */
    color: black;
}

.add-cart-btn:hover {
    background-color: #c0c0c0;
    /* Slight hover effect for secondary button */
}

