.product-image {
    width: 100%;
    height: auto;
    max-width: 300px;
    /* Set a reasonable max width */
    max-height: 250px;
    /* Set a reasonable max height */
    object-fit: cover;
    /* Ensures the image covers the space without distortion */
    border-radius: 12px 12px 0 0;
    /* Rounded corners on the top */
    margin: 0 auto;
    /* Center the image horizontally */
}