/* AuthStyles.css */
.form-container {
    max-width: 500px;
    margin: 80px auto;
    /* Increased top margin for better spacing */
    background: #f7f7f7;
    /* Light grey luxurious background */
    border-radius: 12px;
    box-shadow: 0px 8px 24px rgba(0, 0, 0, 0.1);
    /* Soft shadow */
    padding: 30px;
}

.title {
    text-align: center;
    font-size: 1.8rem;
    margin-bottom: 20px;
    color: #333;
    /* Luxurious dark grey for text */
}

.login-container input,
.register-container input {
    padding: 12px;
    font-size: 1rem;
    border-radius: 8px;
    border: 1px solid #ddd;
    transition: border 0.3s ease;
}

.login-container input:focus,
.register-container input:focus {
    border-color: #888;
    /* Darker border on focus */
}

.button-container {
    text-align: center;
    margin-top: 20px;
}

.button {
    background-color: #3e4095;
    /* Luxurious deep color */
    color: white;
    padding: 10px 20px;
    border: none;
    border-radius: 6px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.button:hover {
    background-color: #282a63;
    /* Darker on hover */
}

.login-button a {
    display: block;
    margin-top: 10px;
    text-decoration: none;
    color: #282a63;
}

.login-button a:hover {
    text-decoration: underline;
}