@import url('https://fonts.googleapis.com/css2?family=Playfair+Display:ital,wght@0,700;1,700&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Roboto+Mono:wght@100..700&family=Rubik:wght@800&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html,
body {
  height: 100%;
  overflow-x: hidden;
  /* Prevent horizontal scroll */
}

body {
  font-family: 'Poppins', sans-serif;
  line-height: 1.6;
  display: flex;
  flex-direction: column;
}

main {
  flex: 1;
}

.navbar {
  font-size: 17px;
  text-transform: uppercase;
  box-shadow: 0 6px 6px -6px gray;
  border-bottom: 1px solid gray;
}

.nav-link {
  font-weight: 300;
}

.active {
  border-bottom: 2px solid #555;
}

.navbar-brand {
  font-family: 'Roboto Mono', monospace;
  font-weight: 700;
  letter-spacing: 3px;
  display: flex;
}

.brand {
  position: relative;
  bottom: 5px;
  margin-left: 5px;
}

.navbar {
  background-color: #111;
  padding: 15px;
  font-size: 18px;
}

.navbar a {
  color: gray;
  text-decoration: none;
  padding: 10px 20px;
  transition: all 0.3s ease;
}

.navbar a:hover {
  color: gold;
  text-shadow: 0px 0px 5px gold;
}

.row {
  display: flex;
  flex-wrap: wrap;
}

.col-md-3,
.col-md-9 {
  padding: 10px;
}

.col-md-3 {
  flex: 0 0 25%;
  max-width: 25%;
}

.col-md-9 {
  flex: 0 0 75%;
  max-width: 75%;
}

@media (max-width: 768px) {

  .col-md-3,
  .col-md-9 {
    flex: 0 0 100%;
    max-width: 100%;
  }
}

.card {
  background-color: #fff;
  border: none;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  border-radius: 12px;
  padding: 20px;
  margin-bottom: 20px;
  transition: transform 0.3s ease;
}

.card:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 15px rgba(0, 0, 0, 0.15);
}


button {
  background-color: #111;
  color: #f5f5f5;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.3s ease;
}

button:hover {
  background-color: gold;
  transform: scale(1.05);
}


input,
select {
  width: 100%;
  padding: 10px;
  margin: 10px 0;
  border-radius: 5px;
  border: 1px solid #ccc;
  transition: all 0.3s ease;
}

input:focus,
select:focus {
  border-color: gold;
  outline: none;
  box-shadow: 0px 0px 5px gold;
}


table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px;
}

table th,
table td {
  padding: 15px;
  border-bottom: 1px solid #ddd;
}

table th {
  background-color: #111;
  color: #f5f5f5;
}

table tr:hover {
  background-color: #f0f0f0;
  transition: background-color 0.3s ease;
}

.fade-in {
  opacity: 0;
  animation: fadeIn 0.5s forwards;
}

@keyframes fadeIn {
  100% {
    opacity: 1;
  }
}

.slide-up {
  transform: translateY(100px);
  opacity: 0;
  animation: slideUp 0.7s forwards;
}

@keyframes slideUp {
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}


.m-3 {
  margin: 30px;
}

.p-3 {
  padding: 30px;
}

/* Page Not Found */
.pnf {
  display: flex;
  min-height: 65vh;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.pnf-title {
  font-size: 100px;
  font-weight: 700;
}

.pnf-heading {
  font-weight: normal;
}

.pnf-btn {
  color: black;
  border: 1px solid black;
  text-decoration: none;
  padding: 10px;
  margin-top: 10px;
}

.pnf-btn:hover {
  background: black;
  color: white;
}

/* FOOTER */
.footer {
  color: white;
  padding-top: 25px;
  background: linear-gradient(to right, #434343, #000000);
  width: 100%;
  height: 120px;
  position: relative;
  /* Changed to relative */
  margin-top: auto;
  /* Ensures footer sticks to the bottom */
}

.footer a {
  text-decoration: none;
  color: white;
  padding: 10px;
  margin-bottom: 10px;

}

.footer a:hover {
  color: #ffefba;
  border-bottom: 1px solid #ffefba;
}

/* Contact */
.contact {
  flex-direction: column;
  display: flex;
  justify-content: center;
  align-items: center;
}

/*Register*/
.product-link {
  text-decoration: none;
  color: black !important;
}



/*CSS*/
/* General styles for luxurious feel */
body {
  font-family: 'Lora', serif;
  color: #333;
  background-color: #f5f5f5;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Layout for containers */
.container-fluid {
  max-width: 1200px;
  margin: auto;
  padding: 20px;
}